.mf-style {
    background-color: white;
    color: $black;

    @media(max-width: 620px) {
        background-color: transparent;
    }

    .btn--call {
        border-color: $red-2;
        color: $red-2;

        &:hover {
            background-color: $red-2;
            color: white;
        }
    }

    .burger {
        .line {
            stroke: $green-1;

            @media(max-width: 620px) {
                stroke: white;
            }
        }
    }

    .header__logo {
        svg path {
            fill: $green-1;

            @media(max-width:620px) {
                fill: white;
            }
        }
    }
}

.mf-fixed {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    z-index: 1;
    transition: linear .3s;
    padding: 0 calc(var(--wScroll) - (100vw - 100%)) 0 0;
}

.svg-transparent {
    fill: none;
}